import React from 'react';

import classes from './MainTitle.css';

const mainTitle = () => (
  <div className={classes.Title}>
    <h1>
      
      <br />
      <br />14  15
      <br />04.2018
    </h1>
    <div className={classes.Subtitle}>  </div>
  </div>
);

export default mainTitle;



